import React from 'react';
import PropTypes from 'prop-types'
import Button from "../Button";
import {useDispatch} from "react-redux";
import {fetchAllPizzas} from "../../redux/actions/selectPizzaModal";

const PizzaCard = ({imageUrl, name, price, about, id}) => {
    const dispatch = useDispatch();

    function onClickHandler(id) {
        dispatch(fetchAllPizzas(id));
    }

    return (
        <div className="pizza-block">
            <div className="pizza-block__content">
                <img
                    className="pizza-block__image"
                    src={imageUrl}
                    alt="Pizza"
                />
                <h4 className="pizza-block__title">{name}</h4>
                <p className="pizza-block__about">{about}</p>
            </div>
            <div className="pizza-block__bottom">
                <div className="pizza-block__price">от {price} ₴</div>
                <Button outline add onClickHandler={onClickHandler.bind(null, id)}>
                    <span>Выбрать</span>
                </Button>
            </div>



            {/*<Button outline add>*/}
            {/*    <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z" fill="white"/></svg>*/}
            {/*    <span>Добавить</span>*/}
            {/*    <i>2</i>*/}
            {/*</Button>*/}
        </div>
    );
};

PizzaCard.propTypes = {
    imageUrl: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
    about: PropTypes.string.isRequired,
}

export default PizzaCard;
